/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body Styling */
body {
  font-family: "Poppins", sans-serif;
  background-color: #f4f7fa;
}

/* Login Page Container */
.login-page {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: url("https://www.stampli.com/wp-content/uploads/2022/06/01-STAMPLI__-expense-accounts-hero-1024x576.png")
    no-repeat center center/cover;
  position: relative;
  overflow: hidden;
}

/* Blurred Overlay for Background */
.login-page::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(3px); /* Adds blur effect */
}

/* Login Form Container */
.login-page form {
  position: relative;
  z-index: 1;
  background-color: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
}

/* Login Form Headings */
.login-page h1 {
  text-align: center;
  color: #333;
  margin-bottom: 1.5rem;
  font-size: 2rem;
}

/* Form Input Fields */
.login-page .ant-form-item {
  margin-bottom: 1.2rem;
}

.login-page .ant-form-item-label > label {
  font-weight: bold;
  color: #333;
}

.login-page input {
  width: 100%;
  height: 45px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.3s ease;
}

.login-page input:focus {
  border-color: #009fe3;
  box-shadow: 0 0 10px rgba(0, 159, 227, 0.2);
}

/* Button Styling */
.login-page .btn-primary {
  background-color: #009fe3;
  color: #fff;
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

.login-page .btn-primary:hover {
  background-color: #007bbd;
  box-shadow: 0 4px 10px rgba(0, 123, 189, 0.3);
}

/* Links */
.login-page a {
  color: #007bbd;
  text-decoration: none;
}

.login-page a:hover {
  color: #005c98;
  text-decoration: underline;
}

/* Flexbox Alignment for Form Footer */
.login-page .d-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
}
